<template>
  <form
    ref="form"
    class="message-areaForm-container"
    @submit.prevent="hanleSubmit"
    id="slb"
  >
    <div class="message-areaForm-Input">
      <input
        type="text"
        v-model="formData.nickName"
        placeholder="用户名称"
        maxlength="10"
      />
      <span class="abs">{{ formData.nickName.length }}/10</span>
      <!-- 直接读取formData.nickName.length的长度 -->
    </div>
    <div class="error">{{ error.nickName }}</div>

    <div class="message-areaForm-textarea">
      <textarea
        v-model="formData.content"
        placeholder="输入内容"
        maxlength="300"
      ></textarea>
      <span class="abs">{{ formData.content.length }}/300</span>
    </div>
    <div class="error">{{ error.content }}</div>

    <div class="button">
      <button type="submit" :disabled="isSumbiting">
        {{ isSumbiting ? "提交中..." : "提交" }}
      </button>
    </div>
  </form>
</template>

<script>
import showMessage from "../../utils/showMessage";
export default {
  data() {
    return {
      isSumbiting: false,
      formData: {
        nickName: "",
        content: "",
        //昵称的错误消息
      },
      error: {
        nickName: "",
        content: "",
        //error的错误消息
      },
    };
  },
  methods: {
    hanleSubmit() {
      this.error.nickName = this.formData.nickName ? "" : "请填写昵称!";
      this.error.content = this.formData.content ? "" : "请填写内容!";
      //在vue中开发，就是要观察数据，
      //上面通过三目运算符进行计算，如果当前昵称，那么设置为"请填写昵称!"否则的话，为空字符串
      //内容也是一样

      if (this.error.nickName || this.error.content) {
        //假设错误里面有内容，则不做任何处理
        return;
      }
      /**
       * 如果没有错误，则进行下面操作
       */
      this.isSumbiting = true;
      //1.将当前按钮状态设为true，true === 当前正在提交中
      this.$emit("submit", this.formData, (successmsg) => {
        /**然后抛出事件，把表单里面的数据当作参数传过去，
         * 第一个参数 type String 事件类型
         * 第二个参数 type data   input里面的value
         * 第三个参数 type callback  外部注册submit事件时，允许有一个回调，参数是一个String
         */
        showMessage({
          content: successmsg,
          //提示文字
          type: "success",
          container: this.$refs.form,
          callback: () => {
            this.isSumbiting = false;
            this.formData.nickName = "";
            this.formData.content = "";
          },
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
