.message-areaForm-container {
  margin-top: 40px;
  .error {
    color: @warn;
    font-size: 16px;
    margin: 10px 0;
    height: 40px;
    line-height: 40px;
  }
  .button {
    button[type="submit"] {
      outline: none;
      padding: 8px 27px;
      border: none;
      color: @fff;
      background: @primary;
      border-radius: 10px;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        background: rgb(56, 161, 96);
        transform: scale(1.2);
      }
      &:disabled {
        cursor: not-allowed;
        background: @gray;
      }
    }
  }
  .message-areaForm-Input input,
  .message-areaForm-textarea textarea {
    position: relative;
    width: 100%;
    outline: none;
    border-radius: 10px;
    padding: 15px 0 15px 5px;
    border: 1px dashed rgb(116, 136, 224);
    &:focus {
      border-color: rgb(240, 43, 207);
    }
  }
  .message-areaForm-textarea {
    width: 100%;
    position: relative;
    textarea {
      height: 150px;
    }
  }
  .message-areaForm-Input {
    width: 300px;
    position: relative;
  }
}
.abs {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 12px;
  color: @gray;
}
</style>
