<template>
  <div class="right-container">
    <ul class="right-list-container">
      <li v-for="(item, i) in list" :key="i">
        <span :class="{ active: item.isSeleted }" @click="hanldClick(item)">{{
          item.name
        }}</span>

        <span
          :class="{ active: item.isSeleted }"
          @click="hanldClick(item)"
          v-if="item.number"
          class="number"
        >
          {{ item.number }}
        </span>
        <rightList :list="item.children" @selected="hanldClick" />
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "rightList",
  //[{name:xxx, children:{name:aaaa,children:{name:bbb}}}]
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    hanldClick(item) {
      if (!item.isSelect) {
        this.$emit("selected", item);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.right-list-container {
  list-style-type: none;
  padding: 0;
  .right-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    cursor: pointer;
  }

  .number {
    margin-left: 1em;
  }
}
.active {
  font-weight: bold;
  color: rgb(79, 108, 236);
}
</style>
