<template>
  <div class="detail-container">
    <Layout>
      <div class="detail-main" ref="detailContainer">
        <DetailMain :blog="data" v-if="data"></DetailMain>
        <BlogCommtent v-if="!loadingShow" />
      </div>

      <template #right>
        <div class="toc-container" v-loading="loadingShow">
          <DetailList :toc="data.toc" v-if="data"></DetailList>
        </div>
      </template>
    </Layout>
  </div>
</template>

<script>
import BlogCommtent from "../../views/Blog/components/blogCommtent.vue";
import DetailMain from "./components/DetailMain";
import DetailList from "./components/DetailList";
import fetchData from "../../mixins/fetchData.js";
import { getBlog } from "../../../api/blog";
import Layout from "@/components/Layout";
import { setTitle } from "../../utils";
import mainScroll from "../../mixins/mainScroll";

export default {
  mixins: [fetchData(null), mainScroll("detailContainer")],
  components: {
    Layout,
    DetailMain,
    DetailList,
    BlogCommtent,
  },
  methods: {
    async fetchData() {
      let resp = await getBlog(this.$route.params.id);
      //得到文章详情页数据
      // resp = null;
      if (!resp) {
        this.$router.push("/404");
        return;
      }
      setTitle.setRouterTitle(resp.title);
      //设置文章title
      return resp;
      //返回数据
    },
  },
};
</script>

<style lang="less" scoped>
.detail-main {
  // min-height: 100%;
  overflow: auto;
  position: relative;
  padding: 30px 20px;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
.detail-container {
  position: relative;
  height: 100%;
}
</style>
