<template>
  <div class="DetailList-container">
    <h3>目录</h3>
    <div class="container"></div>
    <RightList :list="tocWithSelect" @selected="hanldSelected" />
    <!-- 
      1、注意这里list绑定的tocWithSelect，这里之前所绑定的是toc，由于这个toc里面没有isSelect属性
      2、所以通过计算属性写出另外一个以toc为基础的新数组（里面加了isSelect属性，该属性决定了当前标签是否是选中状态）
     -->
  </div>
</template>

<script>
import RightList from "./rightList";
import { shake } from "@/utils/";
export default {
  components: {
    RightList,
  },
  props: {
    toc: {
      type: Array,
    },
  },
  data() {
    return {
      activeArticle: "",
      //给右侧目录中的每一个h3元素添加一个isSelect属性，该属性和data中的activeArticle对比（如果为true，则选中，否则不选中）
      //1、设置一个内部属性activeArticle(由这个属性决定当前是否是选中状态!)
    };
  },
  created() {
    this.setSelectShake = shake(this.setSelect, 300);
    this.$bus.$on("mainScroll", this.setSelectShake);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.setSelectShake);
  },
  computed: {
    //给右侧目录中的每一个h3元素添加一个isSelect属性，该属性和data中的activeArticle对比（如果为true，则选中，否则不选中）
    tocWithSelect() {
      //2、添加一个计算属性tocWithSelect，
      //3、在原来的基础上通过map函数往里面添加一个isSelect属性，该属性决定当前h元素是否被选中!
      const getTOC = (toc = []) => {
        //4、该函数的返回值就是一个新数组，该数组就是在原数组的基础上添加了一个isSelect属性（值为true，代表被选中，否则不选中）;
        return toc.map((t) => ({
          //5、返回的就是新数组
          ...t,
          //6、将其他属性全部展开，
          isSeleted: t.anchor == this.activeArticle,
          //7、添加一个isSelect属性，属性值是不是为true，取决于当前属性中的anchor属性是不是 === this.activeArticle(该值是data中的数据)
          children: getTOC(t.children),
          //8、核心在这里，由于某些目录中可能会有子目录，所以这里用到了递归，再次调用该getTOC函数，将每个目录中到children传进去!
        }));
      };
      return getTOC(this.toc);
      //9、第一次肯定是要把顶层的toc穿进去，也就是顶级目录!
    },

    //1、拿到右侧目录中所有h3元素的dom，添加到新数组中，并返回
    doms() {
      const doms = [];
      //2、设置新数组
      const addToDoms = (toc) => {
        //3、通过一个方法，给我一个toc(该toc是右侧list)
        for (const t of toc) {
          //4、遍历这个toc，
          doms.push(document.getElementById(t.anchor));
          //5、通过t.anchor拿到所有的h3元素
          if (t.children && t.children.length) {
            //6、假设当前toc中的每一项的children(这是子项)有值，并且他的长度不为0，说明他有子项，需要通过递归再次遍历
            addToDoms(t.children);
            //7、、再次调用addToDoms方法，把有子项的t传进去
          }
        }
      };
      addToDoms(this.toc);
      //8、、调用addToDoms函数，把list列表toc传进去
      return doms;
      //9、最后返回这个数组!
    },
  },
  methods: {
    hanldSelected(item) {
      location.hash = item.anchor;
    },
    setSelect(setSelectDom) {
      if (!setSelectDom) {
        return;
      }
      const range = 200;
      for (const dom of this.doms) {
        if (!dom) {
          continue;
        }
        const top = dom.getBoundingClientRect().top;
        if (top >= 0 && top <= range) {
          this.activeArticle = dom.id;
          return;
        } else if (top > range) {
          return;
        } else {
          this.activeArticle = dom.id;
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.DetailList-container {
  position: relative;
  width: 210px;
  padding: 0 20px;
  height: 100%;
  h3 {
    height: 50px;
    line-height: 50px;
  }
}
</style>
