<template>
  <div>
    <MessageArea
      title="评论列表"
      :subTitle="`(${data.total})`"
      :list="data.rows"
      :isListLoading="loadingShow"
      @submit="hanldSubmit"
    />
  </div>
</template>

<script>
import MessageArea from "../../../components/MessageArea/";
import fetchData from "@/mixins/fetchData.js";
import { getComments, postComment } from "../../../../api/blog";
export default {
  mixins: [fetchData({ total: 0, rows: [] })],
  components: {
    MessageArea,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  created() {
    this.$bus.$on("mainScroll", this.hanlderScroll);
  },
  computed: {
    hasMore() {
      return this.data.rows.length < this.data.total;
      //通过计算属性来判断当前评论是否已经加载完成!
      //this.data.rows == 当前已经加载的数据条数
      //this.data.total == 总数
    },
  },
  methods: {
    hanlderScroll(dom) {
      if (this.loadingShow || !dom) {
        return;
      }
      const range = 100;
      const des = Math.abs(dom.clientHeight + dom.scrollTop - dom.scrollHeight);
      if (des <= range) {
        this.fetchMore();
      }
    },
    async fetchMore() {
      if (!this.hasMore) {
        return;
      }
      this.loadingShow = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.loadingShow = false;
    },
    async fetchData() {
      return await getComments(this.$route.params.id, this.page, this.limit);
    },

    async hanldSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("评论成功!");
    },
  },
};
</script>
