export default function (dataDefaultValue = null) {
    return {
        data() {
            return {
                loadingShow: true,
                data: dataDefaultValue,
            }
        },
        async created() {
            this.data = await this.fetchData();
            this.loadingShow = false;
            //异步获取数据，这是语法糖，
            //this.banners = await getBanners() === getBanners().then((r)=>{ this.banners = r});
        },
    }
}